.mobile-subcate {
	padding: 15px 0;
	@mixin subcatecol($color1,$color2) {
		.subcate-col {
			// box-shadow: 0 10px 20px -10px color-rgba($color1,1)
			figure {
				.subcate-img {
					&:before {
						background: linear-gradient(color-rgba($color1,0) 30%,color-rgba($color2,1)); } } } } }

	.subcate-list {
		article {
			&:nth-child(2) {
				@include subcatecol($lred,$dred); }
			&:nth-child(2) {
				@include subcatecol($lred,$dred); } } }
	.subcate-col {
		figure {
			@include pos(rel,null);
			overflow: hidden;
			@include bor-rad(5px);
			.subcate-img {
				display: block;
				@include pos(rel,null);
				@include bor-rad(5px);
				overflow: hidden;
				&:before {
					content: '';
					display: block;
					@include pos(abs, 0 null 0 0);
					right: 0;
					@include bor-rad(5px);
					background: linear-gradient(color-rgba($lblue,0)30%,color-rgba($dblue,1)); }
				img {
					width: 100%; } }
			figcaption {
				@include pos(abs, null 0 0 0);
				padding: 10px 15px;
				// background: linear-gradient(color-rgba($lblue,0),color-rgba($dblue,1) 70%)
				.subcate-name {
					a {
						color: $white;
						font-weight: 500;
						@include fs(14rem); } } } } } }
.product-subcate {
	margin-bottom: 30px;
	ul {
		white-space: nowrap;
		overflow: auto;
		@include disablescroll;
		li {
			display: inline-block;
			vertical-align: top;
			+ li {
				margin-left: 15px; }
			.subcate {
				display: block;
				width: 80px;
				.icon {
					@include flexbox;
					justify-content: center;
					align-items: center;
					width: 80px;
					height: 80px;
					margin: 0px auto 10px;
					@include bor-rad(50px);
					overflow: hidden;
					background: darken($white,3%);
					color: darken($white,60%);
					box-shadow: 0px 10px 20px -10px color-rgba($black,0.1);
					img {
						max-height: 32px;
						max-width: 32px; }
					.fa {
						@include fs(24rem); } }
				.name {
					@include fs(12rem);
					color: $main;
					text-align: center;
					white-space: normal; } }
			&:hover, &.active {
				.subcate {
					.icon {
						background: linear-gradient(to right,$lblue,$dblue);
						box-shadow: 0px 10px 20px -10px $lblue;
						.fa {
							color: $white; }
						img {
							filter: invert(100%); } }
					.name {
						color: $blue; } } } } } }
.product-item {
	height: 100%;
	@include pos(rel,null);
	@include media-breakpoint-down(lg) {
		height: 100%; }
	.btn_compare {
		// display: none
		@include pos(abs,0 null 0 0);
		right: 0;
		@include fs(0);
		visibility: hidden;
		opacity: 0;
		z-index: 10;
		&:before {
			content: '\f00c';
			display: block;
			width: 30px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			border: 1px solid darken($white,10%);
			@include bor-rad(50px);
			@include fs(16rem);
			margin-top: 10px;
			margin-left: 10px;
			color: darken($white,20%); }
		&.active {
			&:before {
				// border-color: color-rgba($white,0)
				background: linear-gradient(to right, $lpurple, $dpurple);
				color: $white; } } }
	// .product-col
	// 	figure
	// 		figcaption
	// 			.popup-info
 }	// 				display: none
.compare-active {
	.btn_compare {
		visibility: visible;
		opacity: 1; } }
.product-col {
	display: block;
	height: 100%;
	box-shadow: 0px 10px 20px -10px color-rgba($black,0.1);
	figure {
		@include pos(rel,null);
		.product-img {
			display: block;
			@include flexbox;
			height: 200px;
			justify-content: center;
			align-items: center;
			align-content: center;
			overflow: hidden;
			picture {
				width: 100%;
				height: 100%;
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center; }
			img {
				max-width: 90%;
				max-height: 90%; } }
		figcaption {
			padding: 10px;
			.product-name {
				@include fs(13rem);
				line-height: 1.3;
				white-space: normal;
				font-weight: 500;
				margin-bottom: 5px;
				color: $black;
				display: -webkit-box;
				height: 0.8125rem*1.3*2;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				@include media-breakpoint-down(lg) {
					// +fs(12rem)
 }					// height: 0.75rem*1.3*2
				a {
					color: $black; } }
			.product-price {
				margin-bottom: 5px;
				@include pos(rel,null);
				.promotion {
					@include pos(abs, 2px 0 null null);
					@include fs(11rem);
					font-weight: 600;
					color: $white;
					padding: 0px 5px;
					@include bor-rad(3px);
					&.off {
						background: linear-gradient(to right, $orange, $deeporange); } }
				.old {
					@include fs(10rem);
					line-height: 1.5;
					color: darken($white,40%);
					text-decoration: line-through; }
				.new {
					@include fs(13rem);
					line-height: 1.5;
					font-weight: 600;
					color: $red;
					@include media-breakpoint-down(lg) {
						@include fs(12rem); } } }
			.product-des {
				@include fs(12rem);
				line-height: 1.3;
				white-space: normal;
				margin-bottom: 5px;
				color: darken($white,40%);
				// display: -webkit-box
				// height: 0.75rem*1.3*3
				// -webkit-line-clamp: 3
				// -webkit-box-orient: vertical
				// overflow: hidden
 }				// text-overflow: ellipsis
			.btn-compare, .compare-button {
				@include mainbutton($lpurple,$dpurple);
				@include fs(13rem);
				line-height: 30px;
				height: 30px;
				padding: 0px 10px;
				margin-top: 5px;
				display: block;
				text-align: center;
				width: 100%;
				@include media-breakpoint-up(xl) {
					display: inline-block;
					width: auto;
					padding: 0px 20px; } }
			ul {
				li {
					padding-left: 10px;
					white-space: nowrap;
					text-overflow: ellipsis;
					overflow: hidden;
					@include pos(rel,null);
					// margin-bottom: 5px
					// &:last-child
					// 	margin-bottom: 0
					&:before {
						content: '';
						display: block;
						width: 4px;
						height: 4px;
						@include bor-rad(50px);
						background: darken($white,60%);
						@include pos(abs, 7px null null 0); } } }
			.popup-info {
				@include media-breakpoint-up(xl) {
					@include pos(abs, 0 null 0 0);
					right: 0;
					padding: 15px;
					background: $white;
					z-index: 10;
					opacity: 0;
					visibility: hidden;
					@include transition(all,0.3s); }
				.name {
					@include fs(13rem);
					font-weight: 500;
					color: $dblue;
					margin-bottom: 10px;
					@include media-breakpoint-down(lg) {
						display: none; } }
				.des {
					@include fs(11rem);
					color: darken($white,60%);
					ul {
						li {
							margin-bottom: 5px; } } } } }
		&:hover {
			figcaption {
				.popup-info {
					opacity: 1;
					visibility: visible; } } } } }
.mobile-product, .product-list, .productlist {
	article {
		@include media-breakpoint-down(sm) {
			&.change {
				flex: 0 0 100%;
				max-width: 100%;
				.product-col {
					figure {
						@include flexbox;
						@include pos(rel,null);
						.product-img {
							width: 120px;
							height: 120px;
							max-height: 120px; }
						figcaption {
							width: calc(100% - 120px);
							// +pos(rel,null)
							display: block;
							.product-name {
								height: unset;
								min-height: unset; }
							.product-price {
								width: 100%;
								@include flexbox;
								align-items: center;
								margin-bottom: 5px;
								@include pos(static,unset);
								.promotion {
									top: 15px;
									right: inherit;
									left: 0; }
								.new {
									order: 1;
									margin-right: 10px; }
								.old {
									order: 2; } }
							.product-des {
								display: block;
								// +media-breakpoint-down(md)
 }								// 	display: none
							.btn-buy {
								@include pos(abs, null 0px 0px null); } } } } } } } }
.productpager {
