			&:hover {
				border-color: $dblue;
				img {
					@include transform(scale3d(1.1,1.1,1)); } } } } }

.popup-addtocart {
	@include pos(fix, null 0 0 0);
	z-index: 1505;
	@include transform(translateY(calc(100% + 15px)));
	@include transition(all 0.4s);
	border-top: 1px solid lighten($cyan,20%);
	background: lighten($cyan,53%);
	padding: 10px 15px;
	@include opacity(0);
	@include media-breakpoint-up(md) {
		border-left: 1px solid lighten($cyan,20%);
		width: 350px;
		@include pos(fix, null 0 0 null);
		left: inherit; }
	&.open {
		@include transform(translateY(0));
		@include opacity(1); }
	.addtocart-success {
		color: $cyan;
		line-height: 1.5;
		padding-bottom: 7px;
		border-bottom: 1px solid lighten($cyan,40%);
		@include fs(14rem);
		.material-icons {
			// +fs(20rem)
			line-height: 1.5;
			margin-right: 10px; } }
	.btn-hidepopup {
		@include pos(abs, 15px 15px null null);
		@include fs(13rem);
		color: darken($white,60%);
		@include transition(all 0.3s);
		cursor: pointer;
		@include media-breakpoint-down(xs) {
			@include pos(abs, 10px 15px null null);
			@include fs(12rem); }
		&:hover {
			color: $lred;
			cursor: pointer; }
		.material-icons {
			@include fs(16rem); } }
	.product {
		padding: 20px 0;
		max-height: 300px;
		height: 300px;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touchs;
		figure {
			margin-bottom: 10px;
			@include flexbox;
			.product-img {
				//display: block
				width: 70px;
				height: 70px;
				background: $white;
				border: 1px solid lighten($cyan,10%);
				@include bor-rad(5px);
				@include flexbox;
				align-items: center;
				justify-content: center;
				object-fit: cover;
				img {
					@include bor-rad(5px);
					max-height: 65px; } }
			figcaption {
				width: calc(100% - 70px);
				padding-left: 10px;
				@include flexbox;
				flex-wrap: wrap;
				align-items: center;
				align-content: center;
				.product-name, .product-option, .product-amount {
					width: 100%;
					@include fs(12rem);
					color: darken($white,60%); }
				.product-name {
					@include fs(13rem);
					line-height: 1.4;
					a {
						color: $black; } }
				.product-amount {
					width: 30%;
					@include media-breakpoint-up(md) {
						width: 100%; } }
				.product-price {
					width: 70%;
					text-align: right;
					margin: 0;
					padding: 0;
					@include fs(18rem);
					font-weight: 600;
					color: $red;
					line-height: 1;
					@include media-breakpoint-up(md) {
						width: 100%;
						@include fs(15rem);
						font-weight: 500;
						text-align: left; } } } } }
	.popup-btnwrap {
		// padding: 10px 15px
		.btn-gotocart {
			@include mainbutton($lred, $dred);
			display: block;
			padding: 0px 20px;
			width: 180px;
			margin: 0 auto; } } }
.input-number {
	@include flexbox;
	width: 100px;
	@include bor-rad(50px);
	border: 1px solid darken($white,10%);
	.btn-spin {
		width: 34px;
		line-height: 34px;
		height: 34px;
		text-align: center;
		@include fs(18rem); }
	input {
		width: calc(100% - 34px - 34px);
		line-height: 34px;
		margin: 0;
		padding: 0;
		border: none;
		background: none;
		text-align: center;
		font-weight: 600; } }
.popup-panel {
	@include pos(fix, 0 null 0 0);
	right: 0;
	z-index: 1500;
	background: $white;
	@include transform(translateY(100%));
	@include transition(all 0.4s);
	@include opacity(0);
	@include media-breakpoint-down(lg) {
		z-index: 9999;
		height: 100vh;
		padding-top: 50px;
		padding-bottom: 60px;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
		// overflow-y: scroll
 }		// visibility: visible
	&.open {
		@include transform(translateY(0%));
		@include opacity(1); }
	.panel-header {
		@include pos(fixed, 0 0 null 0);
		background: $white;
		z-index: 9999;
		@include media-breakpoint-up(lg) {
			@include pos(rel,null); }
		.btn-closepanel {
			padding: 10px 0;
			line-height: 30px;
			text-align: center;
			color: $black;
			margin-right: 15px;
			float: left; }
		.panel-title {
			@include flexbox;
			float: left;
			height: 50px;
			justify-content: center;
			align-items: center;
			align-content: center;
			font-weight: 500; }
		.nav-tabs {
			margin: 0;
			padding: 0;
			float: left;
			width: calc(100% - 40px);
			.nav-link {
				@include flexbox;
				// justify-content: center
				align-items: center;
				align-content: center;
				height: 50px;
				float: left;
				width: calc(100% - 24px);
				+ .nav-link {
					margin-left: 0; }
				&:nth-child(2) {
					width: 24px; } } } }
	.panel-content {
		max-height: calc(100vh - 50px);
		//padding: 15px 0
		@include media-breakpoint-up(xl) {
			overflow: auto;
			-webkit-overflow-scrolling: touch; }
		@include media-breakpoint-down(lg) {
			visibility: visible;
			overflow: auto;
			-webkit-overflow-scrolling: touch;
			max-height: calc(100vh - 60px);
			height: calc(100vh - 60px); }
		.content {
			@include media-breakpoint-down(lg) {
				max-height: calc(100vh - 60px);
				height: calc(100vh -60px);
				overflow-y: scroll;
				-webkit-overflow-scrolling: touch; } } } }
.option-popup {
	@include pos(fix, unset 0 0 0);
