@import "_cart/_cartheader";
@import "_cart/_cartfooter";
@import "_cart/_cartstep";

.cart-title {
	font-weight: 500;
	text-transform: uppercase;
	@include fs(15rem);
	line-height: 1.5;
	color: $black;
	margin-bottom: 15px;
	@include pos(rel,null);
	padding-left: 25px;
	strong {
		color: $lred; }
	.material-icons {
		@include pos(abs, 4px null null 0);
		@include fs(18rem); } }
.cart-page {
	padding-top: 15px;
	// +media-breakpoint-up(md)
	// 	padding: 0
	.cart-button {
		.btn-back {
			@include btnlinear($lblue,$dblue);
			height: 35px;
			line-height: 35px;
			width: 150px;
			margin-bottom: 15px;
			display: block;
			text-align: center;
			@include bor-rad(35px); } }
	.cart-table {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		width: 100%;
		@include media-breakpoint-down(lg) {
			margin-bottom: 15px; }
		th, td {
			background: none;
			@include media-breakpoint-up(lg) {
				padding: 10px; } }
		th {
			display: none;
			@include media-breakpoint-up(lg) {
				display: table-cell;
				vertical-align: middle;
				font-weight: 500;
				@include fs(13rem);
				text-transform: uppercase;
				background: darken($white,5%);
				border-bottom: 1px solid darken($white,10%);
				&:last-child {
					text-align: right; } } }
		tbody {
			tr {
				@include media-breakpoint-down(md) {
					@include flexbox;
					flex-wrap: wrap;
					border-bottom: 1px solid darken($white,10%);
					&:last-child {
						border-bottom: none; } } }
			td {}
			.product {
				width: 100%;
				padding: 10px;
				@include pos(rel,null);
				@include media-breakpoint-up(lg) {
					width: calc(100% - 200px - 100px - 200px); }
				figure {
					@include flexbox;
					.product-img {
						display: block;
						width: 80px; }
					figcaption {
						width: calc(100% - 80px);
						padding-left: 10px;
						.product-name {
							@include fs(14rem);
							font-weight: 500;
							line-height: 1.5;
							margin-bottom: 5px;
							a {
								color: $black; } }
						.product-option {
							@include media-breakpoint-up(lg) {
								@include flexbox;
								flex-wrap: wrap; }
							.label {
								@include fs(13rem);
								display: inline-block;
								vertical-align: middle; }
							.product-size {
								// width: 50%
								margin-bottom: 5px;
								@include media-breakpoint-up(lg) {
									margin: 0; }
								select {
									display: inline-block;
									vertical-align: middle;
									@include bor-rad(3px);
									border: 1px solid darken($white,30%);
									color: $black;
 } }									// font-weight: 600
							.product-color {
								@include media-breakpoint-up(lg) {
									margin-left: 15px;
									@include flexbox;
									align-items: center;
									align-content: center; }
								.color-list {
									display: inline-block;
									vertical-align: top;
									@include media-breakpoint-up(lg) {
										@include flexbox;
										align-content: center;
										align-items: center; } }
								a {
									display: inline-block;
									border: 1px solid darken($white,10%);
									padding: 2px;
									&.active {
										border-color: $blue; }
									+ a {
										margin-left: 3px; } }
								.color-img {
									width: 14px;
									height: 14px; }
 } }								// width: 50%
						.removeproduct {
							@include pos(abs, 8px 10px null null);
							color: $lred;
							@include fs(0);
							@include media-breakpoint-up(lg) {
								@include fs(13rem);
								@include pos(static,unset);
								margin-top: 10px;
								display: block;
								color: darken($white,20%); }
							&:hover {
								color: $red; }
							.material-icons {
								@include fs(16rem);
								line-height: 1; } } } } }
			.price {
				margin-left: 100px;
				width: calc(100% - 200px);
				@include media-breakpoint-up(lg) {
					width: 150px;
					margin-left: 0; }
				.new {
					display: block;
					@include fs(15rem);
					font-weight: 600;
					color: $black;
					line-height: 1.4;
					@include media-breakpoint-up(lg) {
						color: $black;
						font-weight: 500; } }
				.old {
					display: inline-block;
					@include fs(11rem);
					text-decoration: line-through;
					color: darken($white,40%);
					font-weight: 500;
					line-height: 1;
					@include media-breakpoint-up(lg) {
						font-weight: 400;
						color: darken($white,20%); } }
				.sale {
					display: inline-block;
					@include fs(12rem);
					line-height: 1;
					color: $lred; } }
			.amount {
				margin-bottom: 10px;
				@include media-breakpoint-up(lg) {
					margin-bottom: 0;
					width: 100px; }
				.input-number {
					width: 90px;
					background: darken($white,5%);
					border: none;
					overflow: hidden;
					.btn-spin {
						width: 30px;
						height: 30px;
						line-height: 30px;
						background: darken($white,10%); }
					input {
						line-height: 30px;
						width: 30px; } } }
			.total {
				display: none;
				@include fs(15rem);
				font-weight: 600;
				color: $dblue;
				line-height: 1;
				@include media-breakpoint-up(lg) {
					display: table-cell;
					width: 150px;
					text-align: right; } } } } }
.cart-btnwrap {
	@include pos(fix, null 0 0 0);
	z-index: 50;
	padding: 10px 15px;
	background: $white;
	box-shadow: 0 -3px 20px -10px color-rgba($black,0.5);
	border-top: 1px solid darken($white,5%);
	@include media-breakpoint-up(xl) {
		@include pos(static,unset);
		box-shadow: none;
		border-top: none;
		padding: 10px 0;
		margin-bottom: 20px; }
	.total-amount {
		margin-bottom: 10px;
		@include media-breakpoint-down(sm) {
			&:after {
				content: '';
				display: table;
				clear: both;
				min-height: 1; }
			> .clearfix {
				@include flexbox;
				flex-wrap: wrap; } }
		@include media-breakpoint-up(md) {
			@include flexbox;
			justify-content: flex-end;
			clear: both; }
		.label {
			@include fs(14rem);
			font-weight: 500;
			width: 100px;
			@include media-breakpoint-down(sm) {
				float: left; }
			@include media-breakpoint-up(md) {
				width: unset;
				margin-top: 3px;
				margin-right: 10px; } }
		.value {
			width: calc(100% - 100px);
			text-align: right;
			@include fs(18rem);
			font-weight: 600;
			color: $lred;
			@include media-breakpoint-down(sm) {
				float: left; }
			@include media-breakpoint-up(md) {
				width: unset; }
			.des {
				display: block;
				@include fs(12rem);
				color: $main; } } }
	.btn-backstep {
		display: block;
		color: darken($white,40%);
		line-height: 40px;
		float: left;
		@include media-breakpoint-down(sm) {
			margin-right: 15px; }
		&:before {
			content: '\f104';
			margin-right: 10px; }
		&:hover {
			color: $dblue; }
		// +media-breakpoint-down(sm)
 }		// 	display: none
	.btn-nextstep {
		display: block;
		width: 160px;
		@include mainbutton($lblue,$dblue);
		@include media-breakpoint-up(md) {
			float: right;
			padding: 0px 20px; } } }

.cart-lastview {
	// margin-bottom: 140px
	@include media-breakpoint-up(xl) {
		margin-bottom: 20px; }
	.lastview-list {
		white-space: nowrap;
		overflow: auto;
		@include disablescroll;
		@include media-breakpoint-up(xl) {
			white-space: unset;
			margin: 0px -10px;
			overflow: unset;
			.slick-prev, .slick-next {
				@include pos(abs, -45px 10px null null);
				@include fs(0);
				z-index: 5;
				color: $black;
				&:before {
					content: '';
					@include fs(24rem); } }
			.slick-prev {
				right: 30px;
				&:before {
					content: '\f104'; } }
			.slick-next {
				&:before {
					content: '\f105'; } } }
		.item {
			display: inline-block;
			@include media-breakpoint-up(xl) {
				display: block;
				padding: 0px 10px; }
			.product-img {
				display: block;
				width: 70px;
				height: 70px;
				@include flexbox;
				justify-content: center;
				align-items: center;
				border: 1px solid darken($white,10%);
				padding: 5px;
				@include media-breakpoint-up(xl) {
					width: 100%;
					height: unset; } } } } }

.cart-login {
