			width: 350px;
			margin: 0px auto; }
		.form-group {
			@include pos(rel,null);
			margin-bottom: 10px;
			label {
				@include pos(abs, 7px null null 0);
				// margin-bottom: 5px
				color: darken($white,20%);
 }				// font-weight: 500
