	.social-login {
		.module-title {
			// color: darken($white,40%)
			font-weight: 500;
			// text-align: center
			@include media-breakpoint-up(md) {
				@include fs(14rem); } }
		.social-list {
			@include flexbox;
			a {
				display: block;
				width: calc(50% - 5px);
				text-align: center;
				@include bor-rad(50px);
				line-height: 36px;
				padding: 0px 20px;
				font-weight: 500;
				border: 1px solid darken($white,10%);
				+ a {
					margin-left: 10px; }
				.fa {
					margin-right: 10px; }
				&.facebook {
					border-color: $facebook;
					color: $facebook;
					@include media-breakpoint-up(md) {
						// background: $facebook
 }						// color: $white
					&:hover {
						background: $facebook;
						color: $white;
						@include media-breakpoint-up(md) {
 } } }							// background: lighten($facebook,10%)
				&.google {
					border-color: $googleplus;
					color: $googleplus;
					@include media-breakpoint-up(md) {
						// background: $googleplus
 }						// color: $white
					&:hover {
						background: $googleplus;
						color: $white;
						@include media-breakpoint-up(md) {
 } } } } } } }							// background: lighten($googleplus,10%)
.cart-mini {
	@include pos(rel,null);
	margin-bottom: 0;
	@include media-breakpoint-up(xl) {
		margin-bottom: 0; }
	.btn-edit {
		@include pos(abs, 0 0 null null);
		@include fs(13rem);
		color: $dpurple;
		.material-icons {
			@include fs(14rem);
			margin-right: 5px; } }
	.cart-table {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		.group {
			@include flexbox;
			flex-wrap: wrap;
			padding: 10px;
			border-bottom: 1px solid darken($white,10%);
			&:last-child {
				border: none; }
			.product {
				width: calc(100% - 100px);
				figure {
					@include flexbox;
					.product-img {
						display: block;
						width: 70px;
						height: 70px;
						border: 1px solid darken($white,10%); }
					figcaption {
						width: calc(100% - 70px);
						padding-left: 10px;
						.product-name {
							font-weight: 500;
							line-height: 1.5;
							a {
								color: $black; } }
						.product-option {
							color: darken($white,60%);
							@include fs(13rem);
							strong {
								font-weight: 500; } } } } }

			.amount {
				width: 100px;
				text-align: right;
				.price {
					@include fs(13rem);
					font-weight: 600;
					color: $black; }
				.count {
					@include fs(13rem);
					font-weight: 500;
					color: darken($white,60%); }
				.total {
					@include fs(15rem);
					font-weight: 600;
					color: $dblue; } }

			.label {
				@include fs(14rem);
				font-weight: 500;
				width: calc(100% - 150px);
				a.des {
					@include fs(11rem);
					display: block;
					color: $lred;
					font-weight: normal;
					@include media-breakpoint-up(md) {
						@include fs(12rem);
						color: darken($white,40%);
						&:hover {
							color: $lred; } } } }
			.value {
				@include fs(14rem);
				font-weight: 600;
				color: $black;
				text-align: right;
				width: 150px;
				@include media-breakpoint-up(md) {
					font-weight: 500; }
				&.total {
					color: $dblue;
					@include fs(16rem);
					@include media-breakpoint-up(md) {
						font-weight: 600; } } } } } }

.cart-userinfo {
	padding: 15px 0;
	@include media-breakpoint-up(md) {
		padding: 0; } }
.cart-form {
	.form-wrap {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		padding: 10px 15px;
		margin: 0; }
	.form-group {
		@include flexbox;
		flex-wrap: wrap;
		margin-bottom: 10px;
		@include media-breakpoint-up(md) {
			margin-bottom: 15px; }
		.label {
			width: 200px;
			@include media-breakpoint-down(sm) {
				width: 150px; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-bottom: 5px; }
			@include media-breakpoint-up(md) {
				@include flexbox;
				padding: 10px 0; }
			label {
				@include fs(14rem);
				font-weight: 500;
				@include media-breakpoint-up(md) {
 } } }					// +fs(13rem)
		.input {
			width: calc(100% - 200px);
			@include media-breakpoint-down(sm) {
				width: calc(100% - 150px); }
			@include media-breakpoint-down(xs) {
				width: 100%; } }
		input, textarea, select {
			width: 100%;
			border: none;
			border-bottom: 2px solid darken($white,20%);
			padding: 0;
			height: 40px;
			line-height: 40px;
			@include fs(14rem);
			@include bor-rad(0px);
			@include transition(all,0.3s);
			&::-webkit-input-placeholder {
				color: $main;
				opacity: 0.3; }
			&:hover, &:active, &:focus {
				border-color: $main; } }
		select {
			padding: 9px 15px; } }
	.des {
		@include fs(13rem);
		color: darken($white,40%);
		font-style: italic;
		margin-top: 5px;
		@include media-breakpoint-up(md) {
			@include fs(12rem); } }
	.hr {
		width: 100%;
		height: 1px;
		margin: 20px 0;
		background: darken($white,10%);
		padding: 0; }
	.checkbox {
		margin: 10px 0; }
	#buyerinfo {
		display: none; } }

.cart-billexport {
	padding-bottom: 15px;
	.cart-title {
		padding-left: 0;
		label {
			font-weight: 500;
			@include fs(15rem);
			color: $black;
			&:before, &:after {
				top: 4px; } } }
	.bill-form {
		display: none; }
	.form-wrap {
