.cart-coupon {
	padding-bottom: 15px;
	margin-bottom: 15px;
	@include media-breakpoint-up(xl) {
		padding-bottom: 0;
		margin-bottom: 0; }
	.form-wrap {
		margin-bottom: 15px; }
	.input-coupon {
		border-bottom: 2px solid darken($white,20%);
		width: 100%;
		@include flexbox;
		@include media-breakpoint-up(md) {
			max-width: 350px; }
		input {
			border: none;
			padding: 0;
			height: 40px;
