							&:after {
								content: '';
								display: table;
								width: 100%;
								height: 0;
								clear: both; }
							.method-img {
								display: flex;
								justify-content: center;
								align-items: center;
								width: 80px;
								height: 80px;
								padding: 10px 0;
								@include transition(all,0.3s);
								@include media-breakpoint-down(xs) {
									width: 50px;
									border-right: none;
									border: none; } }
							.method-detail {
								padding: 10px;
								width: calc(100% - 80px);
								border-left: 1px solid darken($white,10%);
								@include media-breakpoint-down(xs) {
									width: calc(100% - 50px); }
								.method-name {
									@include fs(14rem);
									font-weight: 600;
									@include media-breakpoint-up(md) {
										@include fs(13rem); } }
								.method-des {
									@include fs(13rem);
									color: darken($white,40%);
									@include media-breakpoint-up(md) {
										@include fs(12rem); } }
								.term {
									padding: 10px 0;
									h3 {
										font-weight: 500;
										color: $main;
										margin-bottom: 10px; }
									.scroll {
										max-height: 100px;
										overflow: auto;
										// padding: 10px
										@include bor-rad(3px);
 } } } } }										// background: lighten($blue,60%)
					&:checked {
						+ label {
							+ .boxContent {
								display: block; }
							.method-info {
								border-color: $blue;
								.method-img {
									border-color: $blue; } } } } }
				.agree {
					@extend .checkbox;
					margin-top: 10px;
					margin-bottom: 10px;
					padding-left: 20px;
					label {
						@include fs(14rem);
						&:before, &:after {
							@include transform(translateY(0%));
							background: none; } } } }
			.method-content {
				display: none;
				border-top: 1px solid darken($white,10%);
				@include fs(13rem);
				padding: 15px; }
			&.active {
				border-color: $dblue;
				.select-group {
					input {
						+ label {
							.method-info {
								.method-detail {
									border-left-color: $dblue; } } } } }
				.method-content {
					display: block;
					border-top-color: $dblue; } } } } }

.lazada-payment {
	@include bor-rad(5px);
	border: 1px solid darken($white,10%);
	overflow: hidden;
	@include media-breakpoint-down(lg) {
		margin-bottom: 30px; }
	&.tragop {
		display: none;
		@include media-breakpoint-down(sm) {
			display: block; } }
	.nav-tabs {
		margin: 0;
		padding: 0;
		border: none;
		background: darken($white,5%);
		border-bottom: 1px solid darken($white,10%);
		.nav-item {
			@include pos(rel,null);
			margin: 0;
			padding: 0;
			border: none;
			@include media-breakpoint-down(sm) {
				width: 33.33%;
				// &:last-child
 }				// 	display: none
			.nav-link {
				margin: 0;
				padding: 0;
				border: none;
				@include bor-rad(0);
				@include flexbox;
				width: 120px;
				height: 100%;
				justify-content: center;
				align-items: flex-start;
				align-content: flex-start;
				color: $main;
				padding-bottom: 30px;
				@include media-breakpoint-down(sm) {
					width: 100%; }
				.icon {
					@include flexbox;
					width: 70px;
					height: 60px;
					justify-content: center;
					align-items: center;
					img {
						max-width: 90%;
						max-height: 90%; } }
				.text {
					width: 100%;
					text-align: center;
					@include fs(11rem);
					padding: 0px 10px; }
				input {
					display: none;
					+ label {
						@include flexbox;
						justify-content: center;
						flex-wrap: wrap;
						&:before {
							content: '';
							display: block;
							width: 14px;
							height: 14px;
							@include bor-rad(50px);
							border: 1px solid darken($white,20%);
							@include pos(abs, null null 10px 50%);
							@include transform(translateX(-50%)); }
						&:after {
							content: '';
							display: block;
							width: 8px;
							height: 8px;
							@include bor-rad(50px);
							background: $lred;
							@include pos(abs,null null 13px 50%);
							@include transform(translateX(-50%));
							opacity: 0; } }
					&:checked {
						+ label {
							&:after {
								opacity: 1; } } } } } } }
	.tab-content {
		// +media-breakpoint-down(sm)
		// 	.tab-pane
		// 		&:last-child
 }		// 			display: none
	.content {
		@include fs(12rem);
		line-height: 1.8;
		padding: 15px; } }

.cart-address {
	@include pos(rel,null);
	margin-bottom: 15px;
	.btn-edit {
		@include pos(abs, 0 0 null null);
		@include fs(13rem);
		color: $dpurple;
		@include media-breakpoint-up(md) {
			color: $lred;
			&:hover {
				color: $red; } }
		.material-icons {
			@include fs(14rem);
			margin-right: 5px; } }
	.address-info {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		padding: 10px;
		margin-bottom: 15px;
		.name {
			font-weight: 500;
			margin-bottom: 5px;
			color: $dblue;
			@include fs(14rem); }
		.phone {
			color: darken($white,60%);
			@include fs(13rem);
			&:hover {
				color: $lred; }
			&:before {
				content: '\f095';
				margin-right: 5px; } }
		.address {
			color: darken($white,60%);
			@include fs(13rem);
			&:before {
				content: '\f041';
				margin-right: 5px; } } } }

.thankyou {
	text-align: center;
	@include media-breakpoint-down(md) {
		margin-bottom: 20px;
		padding: 20px 0; }
	img {
		width: 80%; } }
.payment-success {
	.notice {
		color: $dblue;
		background: lighten($dblue,40%);
		border: 1px solid lighten($dblue,0%);
		padding: 10px 15px;
		@include bor-rad(5px);
		@include pos(rel,null);
		@include flexbox;
		align-items: center;
		margin-bottom: 15px;
		@include media-breakpoint-down(xs) {
			padding: 10px;
 }			// padding-top: 40px
		.fa {
			content: '\f058';
			font-family: 'fontawesome';
			@include fs(18rem);
			line-height: 1;
			@include media-breakpoint-down(xs) {
				@include pos(abs, 10px null null 50%);
				@include transform(translateX(-50%));
				@include fs(24rem);
				display: none; } }
		span {
			width: calc(100% - 40px);
			margin-left: 10px;
			font-weight: 500;
			text-transform: uppercase;
