				// 	width: 15%
 } }				// 	text-align: right
		.total-overview {
			@include flexbox;
			@include fs(13rem);
			@include media-breakpoint-down(sm) {
				display: none; }
			.overview-col {
				width: 33.33%;
				padding: 10px;
				&:nth-child(2) {
					text-align: center; }
				&:nth-child(3) {
					text-align: right; }
				strong {
					color: $lred; } } } }
	.receive-address {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
