///
/// margin-pading settings.
///

@for $i from 0 through 50 {
    @include get-bt-val(mrt, #{$i}r, margin-top, #{$i}rem);
    @include get-bt-val(mrb, #{$i}r, margin-bottom, #{$i}rem);
    @include get-bt-val(pdl, #{$i}r, padding-left, #{$i}rem);
    @include get-bt-val(pdr, #{$i}r, padding-right, #{$i}rem); }

$typesmrpd: 0;
@while $typesmrpd < 205 {
    @include get-bt-val(mrt, #{$typesmrpd}, margin-top, #{$typesmrpd}px);
    @include get-bt-val(mrb, #{$typesmrpd}, margin-bottom, #{$typesmrpd}px);
    @include get-bt-val(pdl, #{$typesmrpd}, padding-left, #{$typesmrpd}px);
    @include get-bt-val(pdr, #{$typesmrpd}, padding-right, #{$typesmrpd}px);
    $typesmrpd: $typesmrpd + 5; }
