.page-title {
	@include fs(28rem);
	font-weight: 600;
	margin-bottom: 20px; }
.hot-news {
	// padding: 30px 0 0
	margin-bottom: 20px;
	font-family: 'Helvetica', Arial, sans-serif;
	.page-title {
		text-transform: uppercase; }
	.title-wrap {
		@include clearfix;
		.small-title {
			@include fs(20rem);
			font-weight: 600;
			margin-bottom: 20px;
			float: left; }
		.btn-viewmore {
			@include fs(14rem);
			float: right;
			color: $blue;
			margin-top: 2px;
			&:after {
				content: '\f105';
				margin-left: 5px; } } }
	.hotnews-list {
		.row {
			margin: 0px -10px; }
		.col-12 {
			padding: 0px 10px; } }
	.news-col {
		margin-bottom: 20px;
		font-family: 'Helvetica', Arial, sans-serif;
		figure {
			@include pos(rel,null);
			@include bor-rad(5px);
			overflow: hidden;
			box-shadow: 0px 20px 70px color-rgba($black,0.3);
			.news-img {
				display: block;
				@include bor-rad(5px);
				overflow: hidden;
				@include pos(rel,null);
				&:before {
					content: '';
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: linear-gradient(135deg, color-rgba($black,0.2),color-rgba($black,0.5)); }
				img {
					width: 100%;
					height: 180px;
					object-fit: cover; } }
			figcaption {
				@include pos(abs, null 10px 10px 10px);
				z-index: 10;
				text-shadow: 0 1px 3px color-rgba($black,0.1);
				.news-name {
					@include fs(14rem);
					font-family: 'Helvetica', Arial, sans-serif;
					font-weight: 500;
					line-height: 1.3;
					display: -webkit-box;
					max-height: 0.875rem*1.3*2;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					a {
						color: $white; } }
				time {
					@include fs(11rem);
					color: $white;
					opacity: 0.6; } } }
		&.big {
			figure {
				.news-img {
					img {
						height: 380px; } }
				figcaption {
					.news-name {
						@include fs(20rem);
						font-family: 'Helvetica', Arial, sans-serif;
						display: -webkit-box;
						max-height: 1.25rem*1.3*2;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
						overflow: hidden;
						text-overflow: ellipsis; } } } } } }
