					padding-right: 10px;
					padding-left: 10px; } } }
		.news-col {
			@include bor-rad(5px);
			overflow: hidden;
			background: darken($white,5%);
			margin-bottom: 15px;
			@include media-breakpoint-down(sm) {
				background: none;
				padding-bottom: 20px;
				margin-bottom: 20px;
				border-bottom: 1px solid darken($white, 10%); }
			figure {
				.news-img {
					picture {
						@include flexbox;
						align-items: center;
						justify-content: center;
						object-fit: cover;
						max-height: 135px;
						@include media-breakpoint-up(md) {
							max-height: 230px; }
						img {
							width: 100%;
							min-height: 135px;
							max-height: 135px;
							object-fit: cover;
							@include media-breakpoint-up(md) {
								min-height: 230px;
								max-height: 230px; } } } }
				figcaption {
					padding: 10px 15px;
					@include pos(rel,null);
					@include media-breakpoint-down(sm) {
						padding: 15px 15px 0 15px; }
					.news-name {
						margin: 0;
						line-height: 1.4;
						font-family: 'Helvetica', Arial, sans-serif;
						@include media-breakpoint-up(md) {
							min-height: 3rem; }
						@include media-breakpoint-down(sm) {
							margin: 0 0 15px; }
						a {
							@include media-breakpoint-up(md) {
								color: $black;
								display: block;
								max-height: 0.875rem*1.4*2;
								@include fs(14rem);
								font-weight: 600;
								line-height: 1.4;
								-webkit-line-clamp: 2;
								-webkit-box-orient: vertical;
								overflow: hidden;
								text-overflow: ellipsis; }
							@include media-breakpoint-down(sm) {
								@include fs(24rem);
								font-weight: 600;
								line-height: 1.4;
								color: #475b6f; } } }
					time {
						display: block;
						line-height: 1.4;
						@include fs(12rem);
						font-weight: 400;
						color: darken($white,40%);
						margin-bottom: 5px;
						@include media-breakpoint-down(sm) {
							display: none; } }
					.news-des {
