					padding: 0;
					figure {
						@include pos(rel,null);
						.video-img {
							width: 100%;
							&:before {
								@include fs(36rem); } }
						figcaption {
							padding: 0;
							@include pos(abs, null 15px 15px 15px);
							a {
								display: block;
								color: $white;
								@include fs(16rem); } } } } }
			figure {
				@include media-breakpoint-up(lg) {
					@include flexbox; }
				.video-img {
					display: block;
					@include pos(rel,null);
					@include bor-rad(5px);
					overflow: hidden;
					@include media-breakpoint-up(lg) {
						width: 70px; }
					&:before {
						content: '\f04b';
						@include pos(abs, 0 null 0 0);
						right: 0;
						@include flexbox;
						justify-content: center;
						align-items: center;
						@include fs(28rem);
						color: $white;
						background: color-rgba($black,0.5);
						@include media-breakpoint-up(lg) {
							@include fs(16rem); } } }
				figcaption {
					padding: 10px 0;
					white-space: nowmal;
					@include fs(13rem);
					font-weight: 500;
