 } }		// background: darken($white,3%)
.small-title {
	@include fs(16rem);
	font-weight: 600;
	color: $black;
	text-transform: uppercase;
	margin-bottom: 15px; }

.home-category {
	padding: 30px 0;
	@include media-breakpoint-down(lg) {
		display: none; }
	article {
		&:nth-child(2) {
			.cate-card {
				figure {
					.cate-img {
						&:before {
							background: linear-gradient(-135deg, color-rgba($lred,0), $dred); } } } } }
		&:nth-child(3) {
			.cate-card {
				figure {
					.cate-img {
						&:before {
							background: linear-gradient(-135deg, color-rgba($lgreen,0), $dgreen); } } } } } }
	.cate-card {
		figure {
			@include pos(rel,null);
			.cate-img {
				//display: block
				@include bor-rad(5px);
				overflow: hidden;
				@include pos(rel,null);
				@include flexbox;
				align-items: center;
				justify-content: center;
				max-height: 180px;
				&:before {
					content: '';
					// display: none
					@include pos(abs, 0 null 0 0);
					right: 0;
					z-index: 5;
					opacity: 0.8;
					background: linear-gradient(-135deg, color-rgba($lblue,0), $dblue); }
				img {
					// filter: brightness(60%)
					width: 100%;
					min-height: 180px;
					max-height: 180px;
					object-fit: cover; } }
			figcaption {
				@include pos(abs, null 15px 15px 15px);
				z-index: 5;
				.cate-name {
					a {
						display: block;
						color: $white;
						text-transform: uppercase;
						font-weight: 500;
						@include fs(18rem);
						&:after {
							content: '\f105';
							margin-left: 10px; } } }
				.des {
					@include fs(13rem);
					font-weight: 300;
					color: $white;
					opacity: 0.6; } } } } }
.home-newproduct {
	margin: 0px -15px 15px;
