	.nav-tabs {
		margin: 0;
		padding: 0;
		border: none;
		display: block;
		white-space: nowrap;
		overflow: auto;
		@include disablescroll;
		@include media-breakpoint-up(lg) {
			margin-bottom: 15px; }
		.nav-item {
			display: inline-block;
			margin: 0;
			padding: 0;
			border: none;
			@include fs(16rem);
			text-transform: uppercase;
			font-weight: 500;
			background: none;
			color: darken($white,40%);
			@include media-breakpoint-down(sm) {
				@include fs(14rem); }
			+ .nav-item {
				margin-left: 20px; }
			&.active {
				color: $black;
				font-weight: 600; } } }
	.category-nav {
		margin-bottom: 20px;
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			opacity: 0.5;
			&:hover {
				opacity: 1; }
			&:before {
				content: '';
				@include fs(28rem); } }
		.slick-prev {
			left: 0;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 0;
			&:before {
				content: '\f105'; } }
		.slick-dots {
			text-align: center;
			@include pos(abs, null 0 -15px 0);
			li {
				display: inline-block;
				width: 6px;
				height: 6px;
				@include bor-rad(50px);
				background: darken($white,20%);
				+ li {
					margin-left: 5px; }
				button {
					display: none; }
				&.slick-active {
					background: $black; } } }
		.category-name {
			text-align: center;
			text-transform: uppercase;
			font-weight: 600;
			line-height: 40px;
 } }			// color: $dblue
	.mobile-product {
		margin-bottom: 15px;

		+ .btn-viewmore {
			margin: -10px 15px 15px; } }
	.btn-viewmore {
		display: block;
		@include mainbutton($lblue,$dblue);
		margin-bottom: 15px;
		line-height: 36px;
		@include media-breakpoint-down(lg) {
			width: 150px;
			margin: 15px auto 0; }
		@include media-breakpoint-up(xl) {
			@include pos(abs, 0px 15px null null);
			padding: 0px;
			@include fs(14rem);
			background: none;
			line-height: 1.5;
			color: $dblue;
			box-shadow: none;
			//display: none
			&:before {
				display: none; }
			&:after {
				content: '\f105';
				margin-left: 10px; }
			&:hover {
				color: $red;
				box-shadow: none; } }
		&:before {
			content: '\f055';
			font-family: 'fontawesome';
			margin-right: 10px; } } }
