.mobile-category {
	@mixin mobile-cate-color($color1,$color2) {
		background: linear-gradient(to right, $color1, $color2);
		box-shadow: 0px 10px 20px -10px color-rgba($color2,1); }
	.mobile-cate-list {
		white-space: nowrap;
		margin: 0px -15px;
		margin-bottom: 15px;
		ul {
			white-space: nowrap;
			overflow: auto;
			padding: 0px 15px 20px;
			@include disablescroll;
			li {
				display: inline-block;
				vertical-align: middle;
				+ li {
					margin-left: 5px; }
				&:nth-child(2) {
					.cate-col {
						@include mobile-cate-color($lred,$dred); } }
				&:nth-child(3) {
					.cate-col {
						@include mobile-cate-color($lgreen,$dgreen); } }
				&:nth-child(4) {
					.cate-col {
						@include mobile-cate-color($lpurple,$dpurple); } }
				&:nth-child(5) {
					.cate-col {
						@include mobile-cate-color($lpink,$dpink); } }
				&:nth-child(6) {
					.cate-col {
						@include mobile-cate-color($lblue,$dblue); } }
				&:nth-child(7) {
					.cate-col {
						@include mobile-cate-color($lred,$dred); } }
				&:nth-child(8) {
					.cate-col {
						@include mobile-cate-color($lgreen,$dgreen); } }
				.cate-col {
					width: 150px;
					@include flexbox;
					@include bor-rad(5px);
					overflow: hidden;
					@include mobile-cate-color($lblue,$dblue);
					.icon {
						width: 60px;
						height: 60px;
						@include flexbox;
						justify-content: center;
						align-items: center;
						//filter: invert(100%)
						img {
							max-width: 60%;
							max-height: 60%; } }
					.name {
						width: calc(100% - 60px);
						@include flexbox;
						white-space: normal;
						align-items: center;
						align-content: center;
						color: $white;
						@include fs(13rem);
						padding-right: 10px;
						text-transform: capitalize;
 } } } } } }						// font-weight: 500

