.product-page-intro {
	@include pos(rel,null);
	@include bor-rad(10px);
	overflow: hidden;
	box-shadow: 0px 10px 20px -10px color-rgba($black,0.5);
	.bg {
		img {
			width: 100%; } }
	.caption {
		@include pos(abs, 0 null 0 0);
		width: 45%;
		background: color-rgba($black,0.3);
		padding: 60px;
		color: $white;
		overflow: auto;
		@include media-breakpoint-down(lg) {
			padding: 40px; }
		@include media-breakpoint-down(md) {
			@include pos(static,null);
			color: $main;
			width: 100%;
			background: color-rgba($black,0.05); }
		@include media-breakpoint-down(sm) {
			padding: 20px; }
		.name {
			@include fs(36rem);
			line-height: 1.2;
			font-weight: 700;
			margin-bottom: 20px;
			@include media-breakpoint-down(lg) {
				@include fs(32rem); }
			@include media-breakpoint-down(sm) {
				@include fs(24rem);
				margin-bottom: 15px; } }
		.content {
			@include media-breakpoint-down(lg) {
				@include fs(13rem); } } } }
