.news-breadcrumb, .breadcrumb-wrap {
	padding: 15px 0;
	.breadcrumb {
		margin: 0;
		padding: 0;
		@include bor-rad(0);
		background: none;
		// white-space: nowrap
		li {
			display: inline-block;
			@include pos(rel,null);
			margin-right: 20px;
			&:after {
				content: '';
				display: block;
				width: 0;
				padding: 0;
				border: 3px solid transparent;
				border-color: transparent $dblue $dblue transparent;
				@include pos(abs, null -10px 6px null); }
			&:nth-child(2) {
				&:after {
					border-color: transparent $red $red transparent; } }
			&:nth-child(3) {
				&:after {
					border-color: transparent $dpurple $dpurple transparent; } }
			a {
				color: darken($white,40%);
				@include fs(13rem);
				&:hover {
					color: $black; } } } } }
