/*Register Form*/

.subscribefrm {
	position: relative;
	.fa.fa-exclamation-triangle {
		color: red;
		@include fs(15rem);
		display: block;
		position: absolute;
		top: 10px;
		right: 15px;
		text-indent: -9999px;
		width: 15px;
		height: 15px;
		line-height: 15px;
		text-align: center;
		&:after {
			content: '\f071';
			font-size: 12px;
			color: red;
			text-indent: 0;
			float: left;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			left: 0; }
		@media only screen and (min-width: 1024px) {
			right: 15px;
			top: 6px; } }
	.fa-exclamation-triangle:before, .fa-warning:before {
		margin-right: 5px; } }

.form-group {
	position: relative;
	.fa.fa-exclamation-triangle {
		// position: absolute
		// top: 13px
		// right: 15px
		// color: transparent
		// width: 15px
		// height: 15px
		color: $red;
		@include fs(14rem);
		text-align: left;
		&:before {
			content: '';
			display: block;
			margin-top: 5px; }
		&:after {
			content: '';
			display: block;
			@include pos(abs, 38px 0 null 0);
			height: 2px;
			width: 100%;
			background: $red;
			z-index: 1; } }
	.fa-exclamation-triangle:before, .fa-warning:before {
		margin-right: 5px; } }

.hidden {
	display: none; }

.share {
	text-align: left;
	clear: both;
	margin-bottom: 15px;
	&.sc-right {
		text-align: right; }
	> div {
		display: inline-block!important;
		margin-bottom: 5px;
		margin-right: 5px;
		vertical-align: middle; }
	.addthis_toolbox {
		margin-top: 2.5px; }
