// **********SEARCH-PAGE**********
.searchresults {
	.pagination {
		text-align: left; }
	.home-banner, .block-newsSpecialList {
		display: none; } }
.search-page {
	.searchresultsummary, .searchcontrols {
		display: none; }
	.pagination {
		margin: 30px 0;
		text-align: left;
		width: 100%;
		li {
			display: inline-block;
			+ li {
				margin-left: 5px; }
			a, span {
				background: darken($white,4%);
				display: block;
				width: 34px;
				height: 34px;
				line-height: 34px;
				@include bor-rad(3px);
				color: $main;
				text-align: center;
				@include transition(all 0.3s);
				&:hover {
					background: $blue;
					color: $white; } }
			&.active {
				a, span {
					background: $blue;
					color: $white; } } } }
	.page-header {
		margin-top: 50px;
		h1 {
			@include fs(24rem);
			font-weight: 600;
			line-height: 1.4;
			margin: 0 0 20px;
			text-align: left; } }
	.searchcontrols {
		margin-bottom: 30px;
		.form-control {
			@include bor-rad(0);
			&:active, &:hover, &:focus {
				border-color: $main; } }
		input[type="submit"] {
			background: $main;
			color: $white;
			@include transition(all 0.3s);
			margin-left: -5px;
			@include bor-rad(0);
			&:hover {
				background: lighten($main,10%); } } }
	.searchresults {
		h3 {
			margin: 0 0 5px;
			a {
				color: $main;
				font-size: 14px;
				font-weight: 600;
				@include transition(all 0.3s);
				&:hover {
					color: $blue; } } } } }
//Pagination
.searchresults {
	.pagination {
		> li {
			list-style: none;
			display: inline-block;
			text-align: center;
			> a,
			span {
				font-size: 0.875rem;
				font-weight: 400;
				margin-left: 10px;
				width: 30px;
				height: 30px;
				line-height: 30px;
				display: block;
				text-align: center;
				text-decoration: none;
				color: darken($white, 60%);
				background-color: darken($white, 5%);
				border: none;
				@include border-radius(3px);
				@include transition(all 0.4s ease);
				&:hover,
				&:focus {
					background-color: $smartwatch;
					color: $white; } }
			&.active {
				>a,
				span {
					background-color: $smartwatch;
					color: $white; } }
			&:first-child {
				a,
				span {
					margin-left: 0; } } } } }
.pages {
	@extend .searchresults;
	text-align: center; }

// **********CAPTCHA**********

.RadCaptcha {
	span {
		@include fs(13rem);
		color: $red;
		margin-bottom: 5px; }
	p {
		margin-top: 0.875rem; }
	img {
		display: inline-block!important;
		margin-right: 0.9375rem;
		vertical-align: middle; }
	input {
		width: 250px; }
	.rcRefreshImage {
		display: inline-block!important;
		vertical-align: middle;
		position: relative;
		color: transparent;
		width: 30px;
		height: 30px;
		&:after {
			content: '\f021';
			font-family: "FontAwesome";
			font-size: 18px;
			color: $black;
			position: absolute;
			top: 50%;
			right: 0;
			left: 0;
			@include transform(translate(0, -50%)); } } }

// COMPONENTS
.radio {
	input {
		display: none;
		+ label {
			padding-left: 25px;
			@include pos(rel,null);
			@include fs(14rem);
			color: darken($white,60%);
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0); }
			&:after {
				content: '';
				display: block;
				width: 8px;
				height: 8px;
				@include bor-rad(50px);
				background: $lred;
				@include pos(abs, 5px null null 3px);
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.checkbox {
	input {
		display: none;
		+ label {
			padding-left: 25px;
			@include pos(rel,null);
			@include fs(14rem);
			color: darken($white,60%);
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0); }
			&:after {
				content: '\f00c';
				display: block;
				width: 14px;
				height: 14px;
				line-height: 12px;
				text-align: center;
				color: $lred;
				@include fs(18rem);
				@include pos(abs, 2px null null -1px);
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
