.payment-info {
	margin-bottom: 30px;
	.title {
		&:before {
			content: '\f283';
			font-family: 'fontawesome';
			@include pos(abs, 7px null null 10px);
			@include fs(18rem); } }
	.cart-form {
		.payment-method {
			margin-bottom: 20px;
			.select-group {
				display: block;
				padding: 0;
				input {
					&:checked {
						+ label {
							+ .boxContent {
								display: block; }
							.method-info {
								border-color: $blue;
								.method-img {
									border-color: $blue; } } } } } } } } }

ul.bankList {
	clear: both;
	height: 202px;
	width: 636px; }
ul.bankList li {
	list-style-position: outside;
	list-style-type: none;
	cursor: pointer;
	float: left;
	margin-right: 0;
	padding: 5px 2px;
	text-align: center;
	width: 90px; }
.list-content li {
	list-style: none outside none;
