header {
	.headermain {
		.colright {
			.searchpanel {
				.item {
					figure {
						figcaption {
							.itemname {
								span {
									display: block;
									font-weight: 700;
									color: $red; } } } } } } } } }
.product-page {
	.product-info {
		.btn-prdWrap {
			@include media-breakpoint-down(lg) {
				@include pos(fix, null 0 0 0);
				padding: 10px;
				z-index: 50;
				background: $white;
				@include flexbox; } }
		.btn-wishlist {
			vertical-align: top;
			margin-top: 10px;
			&:before {
				@include fs(20rem); }
			@include media-breakpoint-down(lg) {
				display: block;
				@include pos(static,null);
				border: none;
				margin: 0;
				order: 1;
				strong {
					width: 100%; } } }
		.btn-buy {
			display: inline-block;
			vertical-align: top;
			@include media-breakpoint-down(lg) {
				display: block;
				@include pos(static,null);
				@include fs(14rem);
				line-height: 40px;
				padding: 0;
				text-align: center;
				width: calc(100% - 60px);
				margin: 0;
				border: none;
				@include bor-rad(0);
				white-space: nowrap;
				order: 2;
				margin-top: 0;
				margin-left: 10px;
				strong {
					width: 100%;
					padding: 0;
					margin: 0 !important;
					&.old {
						margin: 0; } }
				&:before {
					display: none; } } }
		.btn-gopWrap {
			background: #3f474a;
			padding: 10px 15px;
			color: $white;
			.des {
				float: left;
				@include fs(15rem);
				font-weight: 600;
				color: #FFC107;
				// margin-bottom: 5px
				max-width: calc(100% - 140px);
				@include media-breakpoint-down(xs) {
					@include fs(13rem); } }
			.tragop-desc {
				@include fs(13rem);
				color: $white;
				font-style: italic;
				float: left;
				clear: left;
				max-width: calc(100% - 140px);
				@include media-breakpoint-down(xs) {
					@include fs(12rem); }
				a {
					color: $white;
					&:before {
						content: '\f101';
						font-family: 'fontawesome';
						margin-right: 5px; } } }
			.btn-installment {
				float: right;
				display: block;
				font-weight: 600;
				text-transform: uppercase;
				// font-size: 14px
				color: $main;
				background: #FFC107;
				padding: 5px 15px;
				@include bor-rad(3px);
				box-shadow: 0 1px 3px color-rgba($black,0.2);
				margin-top: -16px;
				@include media-breakpoint-down(xs) {
					@include fs(13rem); } } } } }

.other-slide {
	white-space: nowrap;
	overflow: auto;
	.item {
		display: inline-block;
		width: 45%; } }
.lastview-product {
	.lastview-slide {
		white-space: nowrap;
		overflow: auto;
		.item {
			display: inline-block;
			width: 45%; } } }
.toplink-wrap {
	float: left;
	@include flexbox;
	flex-wrap: wrap; }
.toplink {
	.block-check--cart {
		@include pos(abs, 100% null null 0);
		padding: 10px;
		background: $white;
		z-index: 99;
		display: none;
		.btn-closesub {
			display: none; }
		input[type="text"] {
			height: 30px;
			line-height: 30px;
			color: $main;
			padding: 0 10px;
			border: 1px solid darken($white, 15%); }
		input[type="submit"] {
			@include fs(14rem);
			font-weight: 500;
			display: inline-block;
			color: $white;
			padding: 7px 10px;
			background-color: $blue;
			margin-top: 10px;
			border: none;
			@include bor-rad(3px);
			@include transition(all 0.5s);
			&:hover, &:focus {
				background-color: darken($blue, 10%); } }
		&.show {
			display: block; } } }

.homepage {
	.minimenu {
		display: none; } }
.cartpage {
	header, footer {
		display: none; } }
.block-payorder {
	max-width: 100%;
	padding: 15px;
	display: block;
	margin: 30px 15px;
	@include pos(rel,null);
	border: 1px solid darken($white, 15%);
	@include bor-rad(15px);
	&:after {
		content: '';
		display: table;
		clear: both;
		min-height: 1; }
	@include media-breakpoint-up(md) {
		max-width: 600px;
		margin: 40px auto; }
	.pay-infoWrap {
		padding: 15px;
		.item-pay {
			@include flexbox;
			flex-wrap: wrap;
			padding-bottom: 15px;
			margin-bottom: 15px;
			border-bottom: 1px solid darken($white, 15%);
			.col-img {
				@include flexbox;
				width: 100px;
				max-height: 100px;
				align-items: center;
				justify-content: center;
				object-fit: cover;
				float: left;
				img {
					width: 100%;
					max-height: 100px; } }
			.col-info {
				width: calc(100% - 100px);
				padding-left: 15px;
				float: left;
				@include pos(rel,null);
				.prd-name {
					@include fs(16rem);
					font-weight: 500;
					line-height: 1.4;
					color: $main;
					@include media-breakpoint-up(lg) {
						padding-right: 140px; } }
				.prd-price {
					@include fs(18rem);
					font-weight: 600;
					line-height: 1.4;
					color: $red;
					@include media-breakpoint-up(lg) {
						@include pos(abs, 0 0 null null); } } } }
		.pay-info {
			padding-bottom: 15px;
			margin-bottom: 15px;
			border-bottom: 1px solid darken($white, 15%);
			.item-payInfo {
				@include flexbox;
				flex-wrap: wrap;
				margin-bottom: 5px;
				span, strong {
					@include fs(15rem);
					line-height: 1.4;
					float: left;
					width: 50%;
					&:last-child {
						text-align: right; }
 }					// +media-breakpoint-up(md)
				&.total {
					strong:last-child {
						@include fs(18rem);
						font-weight: 600;
						line-height: 1.4;
						color: $red; } } } }
		.bill-info {
			padding-bottom: 15px;
			margin-bottom: 15px;
			border-bottom: 1px solid darken($white, 15%);
			@include fs(15rem);
			font-weight: 500; } }
	.pay-formWrap {
		padding: 0 15px 15px; }
	.pay-form {
		@include flexbox;
		flex-wrap: wrap;
		margin-left: -15px;
		margin-right: -15px;
		.form-group {
			width: 100%;
			padding: 0 15px;
			@include pos(rel,null);
			&:after {
				content: '';
				display: table;
				clear: both;
				min-height: 1; }
			.checkbox {
				width: 100px;
				float: left; }
			label {
				@include fs(14rem);
				font-weight: 400;
				line-height: 1.4;
				margin-bottom: 5px; }
			input[type="text"], select {
				width: 100%;
				border: 1px solid darken($white,15%);
				padding: 0 15px;
				height: 35px;
				line-height: 35px;
				@include fs(14rem);
				@include transition(all, 0.3s);
				@include bor-rad(0);
				&:hover, &:active, &:focus {
					border-color: darken($white,40%); } }
			&.col-2 {
				flex: 0 0 100%;
				max-width: 100%;
				@include media-breakpoint-up(lg) {
					flex: 0 0 50%;
					max-width: 50%; } } }
		input[type="submit"] {
			@include mainbutton($lblue,$dblue);
			display: block;
			padding: 0px 30px;
			@include fs(14rem);
			line-height: 36px;
			height: 36px;
			margin: 0 auto;
			border: none;
			@include transition(all,0.6s);
			&:hover {
				@include mainbutton($dblue,$lblue);
				height: 36px;
				line-height: 36px;
				padding: 0 30px; } } } }
