.product-detail-page {
	.ui-widget-overlay {
		@include pos(fix,0 0 null 0);
		bottom: 0;
		background: rgba(0,0,0,0.9);
		z-index: 100; }
	.ui-dialog {
		background: $white;
		@include pos(rel,null);
		z-index: 1001;
		padding: 20px;
		@include box-shadow(1px 1px 6px 3px rgba(0,0,0,.2));
		.ui-dialog-content {
			min-height: auto!important;
			margin-bottom: 30px;
			text-align: center; }
		.ui-dialog-buttonset {
			text-align: center;
			button {
				@include fs(15rem);
				font-weight: 600;
				color: $white;
				background: $blue;
				display: inline-block;
				padding: 0 15px;
				@include transition(all 0.4s);
				@include btnlinear($lblue,$dblue);
				height: 35px;
				line-height: 35px;
				width: 150px;
				@include bor-rad(35px);
				text-align: center;
				&:hover, &:focus {
					cursor: pointer; }
				+ button {
					margin-left: 15px;
					@include btnlinear(#cdcdcd,#cccccc); } } } } }
.brand-page {
	.brand-hotsale {
		margin-bottom: 30px;
		.btn-loadmore {
			@include mainbutton($lblue, $dblue);
			display: block;
			padding: 0px 20px;
