.slick-slider {
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		z-index: 5;
		color: darken($white, 60%);
		background: rgba($white, 1);
		width: 40px;
		height: 40px;
		line-height: 56px;
		text-align: center;
		@include fs(0);
		opacity: 0;
		cursor: pointer;
		@include bor-rad(50%);
		@include transition(all,0.5s);
		box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);
		@include media-breakpoint-down(md) {
			opacity: 0.5; }
		&:before {
			@include fs(24rem);
			line-height: 1; }
		&:hover {
			background: $white;
			color: darken($white, 60%); } }
	.slick-prev {
		left: 2px;
		&:before {
			content: '\f104';
			font-family: 'fontawesome'; } }
	.slick-next {
		right: 2px;
		&:before {
			content: '\f105';
			font-family: 'fontawesome'; } }
	&:hover {
		.slick-prev, .slick-next {
			opacity: 1;
			@include media-breakpoint-down(md) {
				opacity: 0.5; } } } }
