.product-detail-page {
	padding-bottom: 50px;
	@include media-breakpoint-up(lg) {
		padding-bottom: 0; } }
.productpage {
	padding-bottom: 40px;
	@include media-breakpoint-up(lg) {
		padding-bottom: 0; } }
.fullpage {
	background: $white;
	@include pos(rel,null);
	z-index: 100;
	min-height: 100vh;
	// padding-bottom: 50px
	// overflow: auto
	@include transition(all,0.2s);
	&.open {
		height: 100vh;
		@include transform(translateX(280px)scale3d(0.9,0.9,0.9));
		overflow: hidden;
		// +bor-rad(5px)
		box-shadow: 0 10px 50px color-rgba($black,0.3);
		@include media-breakpoint-down(xs) {
			@include transform(translateX(calc(100% - 70px))scale3d(0.9,0.9,0.9)); } } }
.mobile-menu {
	@include pos(fix, 0 null 0 0);
	width: 330px;
	// z-index: -1
	background: $white url(../img/menubg2.png) left bottom no-repeat;
	background-size: 280px;
	padding: 20px;
	overflow: auto;
	@include transition(all 0.4s);
	@include opacity(0);
	@include media-breakpoint-up(xl) {
		display: none; }
	&.open {
		@include opacity(1); }
	.mobile-login {
		margin-bottom: 20px;
		figure {
			@include flexbox;
			.user-avt {
				display: block;
				width: 70px;
				height: 70px;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover; } }
			figcaption {
				width: calc(100% - 80px);
				@include flexbox;
				align-items: center;
				align-content: center;
				padding-left: 10px;
				flex-wrap: wrap;
				.btn-wrap {
					margin: 0;
					padding: 0;
					ul {
						li {
							&.firstnav {
								a {
									font-weight: 700;
									@include fs(12rem); }
								+ li {
									.btn-register {
										text-align: left;
										&:after {
											content: '\f08b'; } } } } } } }
				.btn-login {
					display: block;
					line-height: 36px;
					color: $white;
					padding: 0px 30px;
					@include bor-rad(50px);
					background: linear-gradient(to right, $lblue, $blue);
					box-shadow: 0px 10px 20px -10px color-rgba($blue,1);
					margin-bottom: 5px; }
				.btn-register {
					display: block;
					width: 100%;
					@include fs(13rem);
					color: darken($white,40%);
					clear: both;
					text-align: center;
					&:after {
						content: '\f105';
						margin-left: 5px; } } } } }
	.menu-content {
		.menu-wrap {
			margin-bottom: 20px;
			ul.menulink {
				> li {
					> a {
						display: block;
						color: darken($white,60%);
						@include fs(13rem);
						font-weight: 500;
						padding: 8px 0;
						@include pos(rel,null);
						.material-icons {
							margin-right: 10px; }
						.notification-amount {
							@include pos(abs, 10px 10px null null);
							background: linear-gradient(135deg,$orange,$red);
							color: $white;
							@include fs(11rem);
							font-weight: 500;
							line-height: 1;
							padding: 2px 5px;
							@include bor-rad(3px); } }
					&.notification {
						&.active {
							a {
								.material-icons {
									@include pos(rel,null);
									&:before {
										content: '';
										width: 10px;
										height: 10px;
										background: $red;
										border: 2px solid $white;
										@include bor-rad(50px);
										@include pos(abs, 2px 0px null null); } } } } } } } } }
	.menu-contact {
		margin-bottom: 40px;
		.company {
			@include fs(13rem);
			font-weight: 700;
			line-height: 1.5;
			color: main;
			margin-bottom: 10px; }
		ul {
			li {
				@include pos(rel,null);
				padding-left: 30px;
				@include fs(13rem);
				line-height: 1.5;
				margin-bottom: 5px;
				color: darken($white,40%);
				.material-icons {
					@include pos(abs, 5px null null 0);
					@include fs(18rem); }
				a {
					color: darken($white,40%); } } } } }
